.product {
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: center;
    justify-content: flex-end;
    max-height: 400px;
    min-width: 100px;
    margin: 10px 10px;
    padding: 10px;
    background-color: white;
    z-index: 1;
}

.product__info {
    height: 100px;
    margin-bottom: 15px;
}

.product__price {
    margin-top: 5px;
}

.product > img {
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button {
    margin: 30px 0 15px 0;
    background-color: #f0c14b;
    border: 1px solid; 
    border-color: #a88734 #9c7e31 #846a29;
    cursor: pointer;
}